import React, { useState } from 'react'
import Multiselect from 'multiselect-react-dropdown';
import justText from '../justText';

function Selections({index, register, errors, update, append, fields, contentRemove}) {
  
  const [contentNames] = useState(['NEVRESM', 'ARAF', 'STD. YASTIK KILIFI', 'OXF. YASTIK KILIFI', 'KIRLENT KILIFI']);
  
  


  return (
    <>
      <div className='my-4'>
        <div>Marka:</div>
        <input type="text" list="brandData"  className='w-64 px-3 py-3' {...register(`products[${index}].brand`, {required: 'Marka Seiniz!'})} placeholder='Marka Se' onKeyPress={(event) => justText(event)} />
        <datalist id="brandData">
          <option value={'Marka Bir'}>Marka Bir</option>
          <option value={'Marka ki'}>Marka ki</option>
          <option value={'Marka '}>Marka </option>
        </datalist>
        <p className='text-red-500 font-semibold text-sm mt-1'>{errors.products?.[index]?.brand?.message}</p>
      </div>

      <div className='my-4'>
        <div>Beden:</div>
        <input type="text" list="sizeData"  className='w-64 px-3 py-3' {...register(`products[${index}].size`, {required: 'Boyut Seiniz!'})} placeholder='Boyut Se' onKeyPress={(event) => justText(event)} />
        <datalist id="sizeData">
          <option value='Beden Bir'>Beden Bir</option>
          <option value="Beden ki">Beden ki</option>
          <option value="Beden ">Beden </option>
        </datalist>
        <p className='text-red-500 font-semibold text-sm mt-1'>{errors.products?.[index]?.size?.message}</p>
      </div>


      <div className='my-4'>
        <div>rn Cinsi:</div>
        <input type="text" list="productTypeData"  className='w-64 px-3 py-3' {...register(`products[${index}].productType`, {required: 'rn Tipi Seiniz!'})} placeholder='rn Cinsi Se' onKeyPress={(event) => justText(event)} />
        <datalist id="productTypeData">
          <option value='rn Cinsi Bir'>rn Cinsi Bir</option>
          <option value="rn Cinsi ki">rn Cinsi ki</option>
          <option value="rn Cinsi ">rn Cinsi </option>
        </datalist>
        <p className='text-red-500 font-semibold text-sm mt-1'>{errors.products?.[index]?.productType?.message}</p>
      </div>

      <div className='my-4'>
        <div>erikler:</div>
        <div className='w-64'>
          <Multiselect
            isObject={false}
            displayValue='value'
            className='max-w-64'
            onKeyPressFn={function noRefCheck(){}}
            onRemove={(selectedList, removedItem) => {
              contentRemove(fields.map((item) => item.contentName

              ).indexOf(removedItem))
            }}
            onSearch={function noRefCheck(){}}
            onSelect={(selectedList, selectedItem) => {
              append({
                contentName: selectedItem,
                contentPiece: '',
                readySizeSelected: false,
                contentReadySize: '',
                contentHeight: '',
                contentWidth: '',
                weightSelected: false,
                contentStitch: ''
              })
            }}
            options={contentNames}
            showCheckbox
            placeholder="erik Se"
            avoidHighlightFirstOption={true}
          />
        </div>
      </div>


      <div className='my-4'>
        <div>Ambalaj:</div>
        <input type="text" list="packageData"  className='w-64 px-3 py-3' {...register(`products[${index}].package`, {required: 'Ambalaj Seiniz!'})} placeholder='Ambalaj Se' onKeyPress={(event) => justText(event)} />
        <datalist id="packageData">
          <option value='Ambalaj Bir'>Ambalaj Bir</option>
          <option value="Ambalaj ki">Ambalaj ki</option>
          <option value="Ambalaj ">Ambalaj </option>
        </datalist>
        <p className='text-red-500 font-semibold text-sm mt-1'>{errors.products?.[index]?.package?.message}</p>
      </div>
    </>
  );
}

export default Selections;