import React from 'react';
import justText from '../justText';
import justNumber from '../justNumber';

function CustomerForm({register, errors}) {
  return (
    <>
      <div className='px-5 flex justify-around w-full h-fit items-start flex-wrap'>
          <div>
            <div>ALICI ADI</div>
            <input {...register ('customerName', {required: 'Alc Ad Giriniz!'})} className='border border-gray-500 rounded text-black py-0.5 px-2' type="text" placeholder="Alc smi Girin" onKeyPress={(event) => justText(event)}/>
            <p className='text-red-500 font-semibold text-sm px-1 mt-1'>{errors?.customerName?.message}</p>
          </div>

          <div>
            <div>ALICI CAR KOD</div>
            <input {...register ('customerCode', {required: 'Alc Cari Kodu Giriniz!'})}  className='border border-gray-500 rounded text-black py-0.5 px-2' type="text" placeholder='Alc Cari Kod' onKeyPress={(event) => justText(event)}/>
            <p className='text-red-500 font-semibold text-sm px-1 mt-1'>{errors?.customerCode?.message}</p>
          </div>


          <div>
            <div>TESLM</div>
            <input type="text" list="deliveryData" {...register ('delivery', {required: 'Teslimat Seiniz!'})} className='w-32 py-0.5 px-1 border border-gray-500' placeholder='Seiniz...' onKeyPress={(event) => justText(event)}/>
            <datalist id="deliveryData">
              <option value="Teslim Bir">Teslim Bir</option>
              <option value="Teslim ki">Teslim ki</option>
              <option value="Teslim ">Teslim </option>
            </datalist>
            <p className='text-red-500 font-semibold text-sm px-1 mt-1'>{errors?.delivery?.message}</p>
          </div>

          <div>
            <div>TESLM N ZEL KOD</div>
            <input {...register ('deliveryCode', {required: 'Teslimat in zel Kod Giriniz!'})} className='border border-gray-500 rounded text-black py-0.5 px-2' type="text" placeholder='Teslim in zel Kod' onKeyPress={(event) => justText(event)}/>
            <p className='text-red-500 font-semibold text-sm px-1 mt-1'>{errors?.deliveryCode?.message}</p>
          </div>

          <div>
            <div>SATICI</div>
            <input type="text" list="sellerData" {...register ('seller', {required: 'Satc Seiniz!'})} className='w-32 py-0.5 px-1 border border-gray-500' placeholder='Seiniz...' onKeyPress={(event) => justText(event)}/>
            <datalist id="sellerData">
              <option value="Satc Bir">Satc Bir</option>
              <option value="Satc ki">Satc ki</option>
              <option value="Satc ">Satc </option>
            </datalist>
            <p className='text-red-500 font-semibold text-sm px-1 mt-1'>{errors?.seller?.message}</p>
          </div>

          <div>
            <div>BANKA</div>
            <input type="text" list="bankData" {...register ('bank', {required: 'Banka Seiniz!'})} className='w-32 py-0.5 px-1 border border-gray-500' placeholder='Seiniz...' onKeyPress={(event) => justText(event)}/>
            <datalist id="bankData">
              <option value="Banka Bir">Banka Bir</option>
              <option value="Banka ki">Banka ki</option>
              <option value="Banka ">Banka </option>
            </datalist>
            <p className='text-red-500 font-semibold text-sm px-1 mt-1'>{errors?.bank?.message}</p>
          </div>


          <div>
            <div>DEME</div>
            <input type="text" list="paymentData" {...register ('payment', {required: 'deme Yntemi Seiniz!'})} className='w-32 py-0.5 px-1 border border-gray-500' placeholder='Seiniz...' onKeyPress={(event) => justText(event)}/>
            <datalist id="paymentData">
              <option value="Nakit">Nakit</option>
              <option value="Kredi Kart">Kredi Kart</option>
              <option value="Altta Input kacak">Altta Input kacak</option>
            </datalist>
            <p className='text-red-500 font-semibold text-sm px-1 mt-1'>{errors?.payment?.message}</p>
          </div>

          <div>
            <div>FATURA NO.</div>
            <input {...register ('billNo', {required: 'Fatura No. Giriniz!'})} className='border border-gray-500 rounded text-black py-0.5 px-2' type="number" placeholder='Fatura No' onKeyPress={(event) => justNumber(event)}/>
            <p className='text-red-500 font-semibold text-sm px-2 mt-1'>{errors?.billNo?.message}</p>
          </div>
        



      </div>
    </>
  )
}

export default CustomerForm;